<template>
  <div id='add-blog'>
    <h1>add-blog</h1>
    <form
      action=""
      v-if="!submited"
    >
      <label>blog title</label>
      <input
        type="text"
        v-model="blog.title"
        required
      >

      <label for="">blog content</label>
      <textarea
        v-model="blog.content"
        name=""
        id=""
        cols="30"
        rows="10"
      ></textarea>
      <div id="check">
        <label for="vue">vue</label>
        <input
          type="checkbox"
          name="vue"
          value='vue'
          id=""
          v-model="blog.checkArr"
        >
        <label for="react">react</label>
        <input
          type="checkbox"
          name="react"
          value='react'
          id=""
          v-model="blog.checkArr"
        >
        <label for="angular">angular</label>
        <input
          type="checkbox"
          name="angular"
          value='angular'
          id=""
          v-model="blog.checkArr"
        >
        <label for="node">node</label>
        <input
          type="checkbox"
          name="node"
          value='node'
          id=""
          v-model="blog.checkArr"
        >
      </div>

      <label for="">作者：</label>
      <select v-model="blog.author">
        <option
          v-for="(author,index) in authors"
          :key='index'
        >{{author}}</option>
      </select>
      <button @click.prevent='post'>添加博客</button>
    </form>

    <div v-if='submited'>
      <h3>your blog sond ok</h3>
    </div>

    <div class="preview">
      <h3>总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>分类：</p>
      <ul>
        <li
          v-for="item in blog.checkArr"
          :key='item.title'
        >{{item}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "addblog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        checkArr: [],
        author: ""
      },
      authors: ["lheng", "路亨", "路哼哼"],
      submited: false
    };
  },
  methods: {
    post() {
      this.$axios
        .post("http://jsonplaceholder.typicode.com/posts", {
          title: this.blog.title,
          body: this.blog.content,
          userId: 1
        })
        .then(data => {
          console.log(data);
          this.submited = true;
        });
    }
  }
};
</script>

