<template>
  <div
    v-theme:column="'wide'"
    id="show-blog"
  >
    <h1>show blog</h1>
    <input
      type="text"
      placeholder="search"
      v-model="search"
    >
    <div
      class="single-blog"
      v-for="blog in filteredBlogs"
      :key="blog.id"
    >
      <router-link :to="'/blog/'+blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <article>
        {{blog.body | snippet}}
      </article>
    </div>
  </div>
</template>

<script>
import $axios from "../axios-auth";
export default {
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    // this.$axios.get("http://jsonplaceholder.typicode.com/posts").then(data => {
    $axios.get("posts").then(data => {
      //本地
      this.blogs = data.data.slice(0, 10);
      console.log(this.blogs);
    });
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    // "to-uppercase": function(value) {
    //   return value.toUpperCase();
    // }
    toUppercase(value) {
      return value.toUpperCase();
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  }
};
</script>

<style>
#show-blog {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #ddd;
}
</style>