<template>
  <div id="single-blog">
    <h1>singleBlog</h1>
    <h1>
      {{blog.title}}
    </h1>
    <article>
      {{blog.body}}
    </article>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    this.$axios.get("posts/" + this.id).then(data => {
      console.log(data);
      this.blog = data.data;
    });
  }
};
</script>

