import vueRouter from 'vue-router'
import ShowBlog from './components/ShowBlog'
import AddBlog from './components/AddBlog'
import SingleBlog from './components/SingleBlog'
const route = [{
        path: '/',
        component: ShowBlog
    },
    {
        path: '/addBlog',
        component: AddBlog
    },
    {
        path: '/blog/:id',
        component: SingleBlog
    }
]

export default route